.contacts__wrap {
  width: 100%;
  padding: 80px 76px 45px 122px;
}

.contacts__inner-wrap {
  max-width: 485px;
}

.contacts__wrapper {
  display: flex;
  border-top: 1px solid rgba(116, 129, 156, 1);
}

.contacts__heading {
  margin: 0 0 55px;
  font-size: 49px;
  letter-spacing: 1px;
}

.contacts__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}

.contacts__item {
  width: 40%;
  margin-bottom: 18px;
}

.contacts__item h3 {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
}

.contacts__item p {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
}

.contacts__item a {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  letter-spacing: -1px;
  color: rgba(16, 38, 84, 1);

  &:hover {
    opacity: 0.6;
  }

  &:active,
  &:focus {
    opacity: 0.3;
  }
}

@media only screen and (max-width: 1199px) {
  .contacts__wrap {
    padding: 80px 76px 40px 135px;
  }

  .contacts__inner-wrap {
    max-width: 555px;
    margin: 0 auto;
  }

  .contacts__wrapper {
    flex-direction: column;
  }

  .contacts__heading {
    margin: 0 0 44px;
    font-size: 42px;
  }

  .contacts__item {
    width: 44%;
    margin-bottom: 23px;
  }

  .contacts__item h3 {
    margin-bottom: 10px;
    font-size: 20px;
  }

  .contacts__item p {
    font-size: 18px;
  }

  .contacts__item a {
    font-size: 18px;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    letter-spacing: 0.5px;
  }
}

@media only screen and (max-width: 767px) {
  .contacts__wrap {
    padding: 73px 0 45px;
  }

  .contacts__heading {
    font-size: 32px;
  }

  .contacts__inner-wrap {
    width: 290px;
  }

  .contacts__list {
    flex-direction: column;
  }

  .contacts__item {
    width: 100%;
    margin-bottom: 23px;
  }

  .contacts__item--one {
    order: 1;
  }

  .contacts__item--two {
    order: 3;
  }

  .contacts__item--three {
    order: 2;
  }

  .contacts__item--four {
    order: 4;
  }
}
