.timetable-content {
  padding: 130px 0;
}

.timetable-content__wrap {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1160px;
  margin: 0 auto;
}

.timetable__workout-item-wrap {
  display: flex;
}

.timetable__workout-time-wrap {
  padding: 0;
  margin: 74px 0 0;
}

.timetable__workout-wrap--second {
  display: none;
}

.timetable__days-wrap {
  display: none;
}

.timetable-content__heading {
  margin: 0 0 65px;
  font-size: 52px;
  text-align: center;
}

.timetable__workout-wrap {
  width: 100%;
  margin: 0 auto;
}

.timetable__days-list {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto 8px;
  padding: 0;
}

.timetable__item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 148px;
  height: 66px;
  margin-right: 10px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 400;
  border: 2px solid rgba(28, 51, 116, 1);

  &:last-child {
    margin-right: 0;
  }
}

.timetable__workout-list-wrap {
  margin-left: 8px;
}

.timetable__workout-list {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
  padding: 0;
}

.timetable__workout-item {
  margin-right: 8px;
  margin-bottom: 8px;
  list-style: none;

  &:last-child {
    margin-right: 0;
  }
}

.timetable__workout-item button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 148px;
  height: 110px;
  margin-right: 9px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 400;
  border: none;
  background-color: rgba(248, 249, 252, 1);
  cursor: pointer;

  &:last-child {
    margin-right: 0;
  }

  &:active,
  &:focus {
    opacity: 0.5;
  }
}

.timetable__workout-time {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 65px;
  height: 110px;
  margin-bottom: 7px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 400;
  border: 2px solid rgba(28, 51, 116, 1);
  background: none;

  &:last-child {
    margin-right: 0;
  }
}

.timetable__select {
  display: none;
}

.timetable__select-custom {
  display: none;
}

@media only screen and (max-width: 1199px) {
  .timetable {
    margin: 0 auto;
    width: 700px;
  }

  .timetable-content {
    padding: 88px 0 0;
  }

  .timetable-content__wrap {
    display: flex;
    flex-direction: row;
    max-width: 678px;
    padding: 0;
  }

  .timetable__workout-list-wrap {
    width: 100%;
    overflow-x: scroll;

    &::-webkit-scrollbar-track {
      background: rgba(254, 235, 239, 1);
    }

    &::-webkit-scrollbar {
      width: 190px;
      height: 5px;
    }

    &::-webkit-scrollbar-thumb {
      background: rgba(237, 2, 51, 1);
      border-radius: 8px;
    }
  }

  .timetable__days-list {
    margin: 0 0 8px;
    padding: 0;
    width: fit-content;
  }

  .timetable__workout-item {
    margin-right: 11px;
  }

  .timetable-content__heading {
    margin: 0 0 47px;
    font-size: 45px;
  }

  .timetable__workout-wrap {
    padding: 0;
  }
}

@media only screen and (max-width: 767px) {
  .timetable-content {
    padding: 72px 0 0;
  }

  .timetable-content__heading {
    font-size: 34px;
  }

  .timetable-content__wrap {
    display: none;
  }

  .timetable__workout-wrap--second {
    display: flex;
    height: 355px;
    align-items: flex-end;
  }

  .timetable__workout-list-wrap {
    width: 69%;
    overflow: hidden;
  }

  .timetable__workout-list {
    width: 100%;
  }

  .timetable__workout-time-wrap {
    margin: 74px 0 0 14px;
  }

  .timetable__workout-item {
    display: flex;
    flex-direction: row;
    width: 100%;
    opacity: 0;

    &--hide {
      opacity: 1;
    }
  }

  .timetable__workout-item,
  .timetable__workout-item button {
    width: 100%;
    height: 79px;
  }

  .timetable__workout-time {
    width: 60px;
    height: 80px;
    border: 2px solid rgba(28, 51, 116, 1);
    opacity: 0.5;

    &--hide {
      opacity: 1;
    }
  }

  .timetable__select-wrap {
    width: 100%;
    overflow: auto;
  }

  .timetable__select {
    position: absolute;
    right: 0;
    height: 60px;
    left: 0;
    width: 71%;
    margin: 0 0 0 76px;
    border: 2px solid rgba(28, 51, 116, 1);
  }

  table tbody {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .timetable__select-custom {
    position: relative;
    left: 0;
    right: 0;
    display: block;
    width: 69%;
    margin: 0 auto 0 82px;
  }

  .timetable__current-option {
    width: 100%;
    height: 60px;
    margin-right: auto;
    padding: 16px 50px;
    border: 2px solid rgba(28, 51, 116, 1);
    cursor: pointer;
  }

  .timetable__custom-option {
    position: absolute;
    top: 58px;
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: calc(100% - 48.5px);
    margin: 0;
    padding: 0;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    border: 2px solid;
    border-top: none;
    z-index: 10;
  }

  .timetable__custom-option li {
    display: flex;
    justify-content: center;
    border-bottom: 2px solid rgba(28, 51, 116, 1);
    width: 100%;
    padding: 12px 0;
    cursor: pointer;

    &:last-child {
      border-bottom: none;
    }
  }

  .timetable__current-option span {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    width: 100%;
    padding-right: 45px;
    font-family: "Roboto Condensed", "Arial", sans-serif;
  }

  .timetable__img {
    position: absolute;
    right: 0;
    top: 0;
    width: 51px;
    height: 60px;
    background-color: white;
    border: 2px solid rgba(28, 51, 116, 1);
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      top: 45%;
      bottom: 0;
      left: 0;
      right: 0;
      width: 19px;
      height: 10px;
      margin: 0 auto;
      background-image: url("../img/icon-dropdown-arrow.svg");
      background-repeat: no-repeat;
      background-size: 19px 10px;
      background-position: 0 0;
    }
  }

  .timetable__img--up::before {
    display: block;
    background-image: url("../img/icon-dropup-arrow.svg");
  }
}
